import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const Navbar = () => {
    const [show, setShow] = useState(false);

    function showSwitch() {
      return setShow(!show)
    }

    

  return <>
    <div className='navbar'>
        <div className="logo">
            Log Dashboard
        </div>
        <div className={show ? "links active" : "links"}>
            <Link onClick={ () => showSwitch()} to="/">Grafiek</Link> 
            {/* <Link onClick={ () => showSwitch()} to="/minimarket">MiniMarkets</Link>
            <Link onClick={ () => showSwitch()} to="/supermarket">SuperMarkets</Link> */}
            <Link onClick={ () => showSwitch()} to="/tablepage">Tabel</Link>
        </div>
        <div onClick={ () => showSwitch()} className={show ? 'bars-button active' : 'bars-button'}>
            <span></span>
            <span></span>
            <span></span>
        </div>
    </div>
  </>
};

export default Navbar;