import React, { useState, useEffect } from 'react';

const Table = () => {
    const [users, setUsers] = useState([]);

    useEffect(() => {
        fetch('https://autographics.herokuapp.com/users', {method: "GET", redirect: "follow"})
        .then(res => res.json())
        .then(json => {
            console.log(json);
            setUsers(json);
        })
        .catch((err) => console.log(err));
    }, [])

  return (
    <div className='table_container'>
        <table>
            <thead>
                <tr>
                    <th></th>
                    <th>Username</th>
                    <th>Producten</th>
                    <th>Prijzen</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                {users.map((users) => 
                <tr>
                    <td></td>
                    <td>{users.user}</td>
                    <td>{users.product}</td>
                    <td>{users.price}</td>
                    <td></td>
                </tr>
                 )}
            </tbody>
        </table>
    </div>
  )
}

export default Table;