import React from 'react'
import BarChart from '../components/BarChart';
import { useState, useEffect } from 'react';


const ActionChart = () => {
    const [data, setData] = useState([]);

    useEffect(() => {
        fetch('https://autographics.herokuapp.com/data', {method: "GET", redirect: "follow"})
        .then(res => res.json())
        .then(json => {
            console.log(json);
            setData(json);
        })
        .catch((err) => console.log(err));
    }, [])


  return (
    <div className='action_container'>
        <h1>Totale Activiteiten Gebruikers</h1>
        <BarChart labels={data.length === 0 ? ["pink"] : data[0].labels}
        data1={data.length === 0 ? [0,0,0,0,0,0] : data[0].data[0].values}
        data2={data.length === 0 ? [0,0,0,0,0,0] : data[0].data[1].values}
         />
        <button onClick={ () => window.location.reload()}>Refresh chart</button>
    </div>
  )
}

export default ActionChart